import React from 'react';
import axios from 'axios';
import { Spin, Empty, Button, Card, Row, Col, Space, Descriptions, Carousel } from 'antd';

import { UserContext } from '../../contexts';
import { UseContext } from '../../hoc';
import { alternatorApi } from '../../config';
import { UiRestriction } from '../../components';
import { alternatorlist as cms } from '../../cms';

const { Grid } = Card,
  { Item } = Descriptions;

const cardDetailsMapping = {
  part: [
    {
      key: 'ampLabel',
      value: 'amp',
    }, {
      key: 'connectionsLabel',
      value: 'connections',
    }, {
      key: 'fanTypeLabel',
      value: 'fanPulleyType',
    }, {
      key: 'fixingPointLabel',
      value: 'fixingPoint',
    }
  ],
  vehicle: [
    {
      key: 'fuelTypeLabel',
      value: 'fuelType',
    }, {
      key: 'transmissionLabel',
      value: 'transmissionType',
    }, {
      key: 'startLabel',
      value: 'startYear',
    }, {
      key: 'endLabel',
      value: 'endYear',
    }, {
      key: 'makeLabel',
      value: 'make',
    }, {
      key: 'modelLabel',
      value: 'model',
    }, {
      key: 'engineCodeLabel',
      value: 'engineCodes',
    }
  ],
}

class AlternatorListing extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      loading: false,
      error: null,
    }
  }

  async componentDidMount() {
    console.log("::::::", this.stateprops)
    try {
      const { data } = await axios.get(alternatorApi.list);
      this.setState({ loading: false, data });
    } catch (err) {
      this.setState({ loading: false, error: err })
    }
  }

  handleAdd = () => {
    const { history } = this.props;
    history.push('/alternators/add')
  }

  displayDescriptionItems = (config, data) => {
    return config.map(({ key, value }) => (
      <Item label={cms[key]}>
        {Array.isArray(data[value]) ? data[value].join(',') : data[value]}
      </Item>
    ))
  }

  render() {
    const { loading, error, data } = this.state;
    if (loading) {
      return (
        <div className="example">
          <Spin />
        </div>
      )
    }

    if (data.length === 0) {
      return (
        <Empty>
          <UiRestriction requireAdmin>
            <Button onClick={this.handleAdd}>{cms.addAlternatorButton}</Button>
          </UiRestriction>
        </Empty>
      )
    }
    return (
      <div>
        <Space direction="vertical" size="large" style={{ width: '100%' }}>
          {data.map(d => (
            <Card
              size="middle"
            >
              <Grid style={{ width: '40%' }}>
                <Row>
                  <Col span={24}>{d.code}</Col>
                  <Col span={24}>{d.name}</Col>
                  <Col span={24}>
                    <Carousel
                      autoplay
                      style={{ height: 300, maxWidth: 490 }}
                    >
                      {d.imgUrl.map(url => <img src={url} style={{ height: 300, width: 490 }} />)}
                    </Carousel>
                  </Col>
                </Row>

              </Grid>
              <Grid style={{ width: '60%' }}>
                <Descriptions
                  column={1}
                  bordered
                  title="Part Details"
                >
                  {this.displayDescriptionItems(cardDetailsMapping.part, d)}
                </Descriptions>
              </Grid>
              <Grid style={{ width: '60%' }}>
                <Descriptions column={1} bordered>
                  {this.displayDescriptionItems(cardDetailsMapping.vehicle, d)}
                </Descriptions>
              </Grid>
            </Card>
          ))}
        </Space>

      </div>
    )
  }
}

export default UseContext(UserContext)(AlternatorListing);